import { Container, Typography, Grid, Stack, Tab, Tabs, Box, Tooltip, IconButton } from '@mui/material';
import { Icon } from '@iconify/react';
// ----------------------------------------------------------------------

export default function Roles() {
  return (
    <Stack direction="row" spacing={1}>
      <Stack>
        <Typography sx={{ fontSize: '12px', color: '#919EAB' }}>Role</Typography>
        <Typography sx={{ fontSize: '14px' }}>Global Admin</Typography>
        <Typography sx={{ fontSize: '12px', color: '#919EAB' }}>Access Level</Typography>
        <Icon icon="mdi:view-headline" color="#c4c4c4" width="24" rotate={1} />
      </Stack>
      <Stack style={{ marginTop: '60px' }}>
        <Icon
          icon="mdi:dots-vertical"
          style={{
            p: '1px',
            fontSize: '20px',
            color: '#212B36',
            border: '1px solid #919EAB24'
          }}
        />
      </Stack>
    </Stack>
  );
}
